import React, { useRef, useState } from 'react';
import { observer, Observer } from 'mobx-react';
import { runInAction } from 'mobx';
import ImageChange001Store from '../stores/ImageChangeStore';
import who from '../img/who.jpg';
import imgPaths from '../img/img'; //   

import Button from '@mui/material/Button';
import Snackbar from '@mui/material/Snackbar';
import { Alert } from '@mui/material';

const ImageChange001 = observer(() => {
  const store = new ImageChange001Store();
  const [randomPic, setRandomPic] = useState(who); //   'who' 
  const [backgroundImage] = useState(store.backgroundImg);
  const [savedImg, setSavedImg] = useState([]); //  URL  
  const ArrayLength = 3; //     
  const [, ] = useState('');
  const inputRef = useRef();
  const [openSuccessAlert, setOpenSuccessAlert] = useState(false); //   
  const [openErrorAlert, setOpenErrorAlert] = useState(false); //   



  const changeImage = () => {
    let randomImgUrl;
    do {
      const randomIndex = Math.floor(Math.random() * imgPaths.length);
      randomImgUrl = imgPaths[randomIndex].imageUrl;//   
    } while (savedImg.includes(randomImgUrl)); //            do 


    console.log("Selected image:", imgPaths.find(img => img.imageUrl === randomImgUrl));
    runInAction(() => {
      // const updatedSavedImg = [...savedImg]; //  savedimg    
      // if (updatedSavedImg.length >= ArrayLength) { //     3   
      //   updatedSavedImg.shift(); //     shift() ==   
      // }
      // updatedSavedImg.push(randomImgUrl); //   
      // setSavedImg(updatedSavedImg); //      
      setRandomPic(randomImgUrl);//   
    });
  };

  const handleKeyPress = (event) => { // Enter   
    if (event.key === 'Enter') {
      runInAction(() => {//\
        const inputValue = event.target.value;// 
        console.log(":", inputValue);
        (inputValue);//    
        const currentImgName = imgPaths.find(img => img.imageUrl === randomPic).name;//randomPic   imageUrl    
        if (currentImgName === inputValue) { //  
          setOpenSuccessAlert(true); //   
          inputRef.current.value = "";
          const updatedSavedImg = [...savedImg];
          if (updatedSavedImg.length >= ArrayLength) {
            updatedSavedImg.shift();
          }
          updatedSavedImg.push(randomPic);
          setSavedImg(updatedSavedImg);
        }
        else {
          setOpenErrorAlert(true); //   
          inputRef.current.value = "";
        }
        //  
        changeImage();
      });
    }
  };




  return (
    <>
       <section style={{ backgroundImage: `url(${backgroundImage})`, width: '100%', height: '100vh', }}>
        <div style={{ height: '100%', display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center' }}>
          <div style={{
            marginBottom: '10px',
            fontSize: '40px',
            color: 'white',
            border: '2px solid white',
            padding: '10px',
            fontWeight: 'bold',
            textShadow: '2px 2px 4px rgba(0,0,0,1)',
            boxShadow: "10px 10px 10px 8px rgba(255,255,255,0.5)"            
          }}>
             ?
          </div>
          <div style={{ display: 'flex', justifyContent: 'center' }}>
            <Back />
            {/*     */}
            <div>
              <Observer>
                {() => (
                  <img
                    src={randomPic}
                    alt=""
                    width="350px"
                    height="350px"
                    style={{
                      position: 'absolute',
                      top: '50%',
                      left: '50%',
                      transform: 'translate(-50%, -70%)',
                      zIndex: "2",
                      
                    }}
                  />
                )}
              </Observer>

            </div>
            {/*   */}
            <div>
              <div style={{
                marginLeft: '190px',
                fontSize: '20px',
                color: 'white',
                border: '2px solid white',
                padding: '5px',
                fontWeight: 'bold',
                textShadow: '2px 2px 4px rgba(0,0,0,1)',
                boxShadow: "10px 10px 10px 8px rgba(255,255,255,0.5)"     
              
              }}>
                 
              </div>
              <div style={{ display: 'flex', flexDirection: 'column-reverse' }}>
                {savedImg.map((imgUrl, index) => (
                  <img
                    key={index}
                    alt=""
                    src={imgUrl}
                    width="100"
                    height="100"
                    style={{ margin: '10px', marginLeft: '200px', boxShadow: "8px 10px 10px 8px rgba(0,0,0,0.5)", }}
                  />
                ))}
              </div>
            </div>
          </div>
          <Observer>
            {() => (
              <input
                ref={inputRef}
                style={{
                  width: "200px",
                  height: "35px",
                  fontSize: "30px",
                  position: "absolute",
                  top: '50%',
                  left: '50%',
                  transform: 'translate(-50%, 400%)',
                  zIndex: "2",
                }}
                onKeyPress={handleKeyPress}
                
              />
            )}
          </Observer>
          <Button variant="contained" onClick={changeImage} style={{ backgroundColor: "#faa693", marginTop: "80px", width: "200px", height: "50px", fontSize: "25px", fontWeight: "bold" }}></Button>
          {/*  */}
          <Snackbar open={openSuccessAlert} autoHideDuration={2000} onClose={() => setOpenSuccessAlert(false)} anchorOrigin={{ vertical: 'top', horizontal: 'center' }}>
            <Alert severity="success" sx={{ width: '100%' }}>
              !
            </Alert>
          </Snackbar>
          <Snackbar open={openErrorAlert} autoHideDuration={2000} onClose={() => setOpenErrorAlert(false)} anchorOrigin={{ vertical: 'top', horizontal: 'center' }}>
            <Alert severity="error" sx={{ width: '100%' }}>
              .   !
            </Alert>
          </Snackbar>

        </div>
      </section>
    </>
  );
});

function Back() {
  const styles = {
    backgroundColor: "#ffffff",
    color: "white",
    padding: "10px",
    borderRadius: "2px",
    width: "400px",
    height: "500px",
    justifyContent: "center",
    boxShadow: "10px 10px 10px 10px rgba(0,0,0,0.5)",
    position: "relative",
    zIndex: "1",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)"
  };

  return (
    <div style={styles}></div>
  );
}

export default ImageChange001;